.header {
  width: 100%;
  // min-width: 1024px;
  // max-width: 1440px;
  min-height: 825px;
  background-image: url("../img/background-bike-desctop@1x.png");
  background-size: cover;
  background-color: rgb(29, 29, 29);
  // background-color: linear-gradient(#000000 50%, #141414 80%);
  // background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/background-bike-desctop@2x.png");
  }

  @media (max-width: $tablet-width) {
    background-image: url("../img/background-bike-tablet@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/background-bike-tablet@2x.png");
    }
  }

  @media (max-width: $mobile-width) {
    background-image: url("../img/background-bike-mobile@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/background-bike-mobile@2x.png");
    }
  }
}

