.header__logo-wrap {
  width: 100%;
  min-width: 1024px;
  max-width: 1166px;
  min-height: 0;
  margin: 0 auto;
  position: relative;
}

.header__logo {
  position: absolute;
  width: 68px;
  height: 21px;
  bottom: -338px;
  left: 144px;
}

.header__nav {
  width: 100%;
  min-width: 1024px;
  max-width: 1166px;

  padding: 0 146px;
  margin: 0 auto;
}

.header__list {
  @include list-reset;
  height: 62px;
  padding-left: 19px;
  padding-right: 19px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.header__item a {
  box-sizing: border-box;
  display: block;
  height: 62px;
  padding-top: 20px;
  color: $light-1;
  letter-spacing: -0.3px;
}

.header__item a:hover,
.header__item a:focus {
  color: $basic;
}

.header__item a:active {
  color: rgba($basic, 0.5);
}

.header__inner {
  width: 100%;
  min-width: 1024px;
  max-width: 1166px;
  min-height: 763px;
  margin: 0 auto;
  padding: 0 143px;
  padding-top: 243px;
}

.header__inner h1,
.header__inner p {
  @include text-reset;
  width: 393px;
}

.header__inner h1 {
  font-weight: 700;
  font-size: 46px;
  line-height: calc(64 / 46);
  letter-spacing: -0.3px;
  color: $light-1;
  padding-top: 65px;
  margin-bottom: 23px;
}

.header__inner p {
  font-weight: 300;
  font-size: 20px;
  line-height: calc(32 / 20);
  letter-spacing: -0.1px;
  color: $light-1;
}
